.cursor-on .cursor > span.cursor--readmore, .cursor-on .cursor > span.cursor--viewall, .cursor-on .cursor > span.cursor--play, .cursor-on .cursor > span.cursor--pause, .cursor-on .cursor > span.cursor--drag {
  display: flex;
